.offers {
	display: flex;
	justify-content: space-between;
	margin: 50px 0 25px;

	.offers__left, .offers__right {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	.offers__element {
		position: relative;

		img {
			display: block;
			max-width: 100%;
		}

		.offers__category {
			font-size: 13px;
			line-height: 36px;
			letter-spacing: 0.65px;
			font-weight: 500;
			color: $offers-color;
		}

		.offers__name {
			font-size: 19px;
			line-height: 24px;
			letter-spacing: 1.425px;
			font-weight: 700;
			color: $offers-color;
			text-transform: uppercase;
		}

		a {
			@include btn() {
				margin-top: 20px;
			};
		}

		p {
			margin: 10px 0;
		}
	}

	.offers__info {
		position: absolute;
		z-index: 1;
		left: 30px;
	}

	.offers__new {
		position: absolute;
		right: 10px;
		top: 10px;
		font-size: 11px;
		line-height: 14px;
		padding: 5px 10px;
		background-color: $black;
		color: #fff;
		text-transform: uppercase;

		&::before {
			content: "";
			border: 10px solid transparent;
			border-left: 10px solid $black;
			position: absolute;
			left: 0;
			bottom: -9px;
		}
	}

	.offers__center {
    margin: 0 2.1%;
		.offers__info {
      top: 105px;
		}
	}

	.offers__left {

		.offers__info {
      left: 0;
		  bottom: 15px;
		  width: 100%;
		}
		
		.offers__two-get {
			text-align: center;
			font-size: 22px;
			line-height: 25px;
			text-transform: uppercase;
			letter-spacing: 1.65px;
			font-weight: 700;
			color: $offers-color;
			margin: 0;
		}
	}

	.offers__right {

		.offers__info {
			top: 50px;

			.offers__category {
				margin: 0;
			}

			.offers__name {
				margin: 0;
			}
		}
	}

	.offers__element--glasses {

		.offers__info {
			left: 0;
			bottom: 10px;
			width: 100%;
			top: auto;
		}
		
		p {
			text-align: center;
			color: $offers-color;
			text-transform: uppercase;
			font-weight: 700;
			font-size: 13px;
			line-height: 25px;
			letter-spacing: 0.65px;
		}
	}
}

@media (max-width: 1024px) {
	.offers {
		flex-wrap: wrap;

		.offers__right {
			flex-basis: 100%;
			display: block;

			.offers__element {
				max-width: 371px;
				margin: 20px auto;
			}
		}

		.offers__center {
			margin: 0;
		}
	}

}

@media (max-width: 800px) {
	.offers {

		.offers__left {
			flex-basis: 100%;
			display: block;

			.offers__element {
				max-width: 277px;
				margin: 20px auto;
			}
		}

		.offers__center {
			flex-basis: 100%;
			display: block;

			.offers__element {
				max-width: 470px;
				margin: 0 auto;
			}
		}
	}
}

@media (max-width: 768px) {
	.offers {
		margin: 25px 0;

		.offers__center {

			.offers__info {
				top: 50px;
			}
		}

		.offers__right {

			.offers__info {
				top: 15px;
			}
		}

		.offers__element--glasses {

			.offers__info {
				top: auto;
			}
		}
	}
}
